// Initialize Firebase
const app = initializeApp(firebaseConfig);
//const database = getDatabase(app);

export const store = new Vuex.Store({
    state: {
        count: 0,
        postId: '',
        chapContents: null,
        //database: getDatabase(app),
        db: getFirestore(app)
    },
    mutations: {
        increment(state) {
            state.count++
        },
        setPostId (state, id) {
            state.postId = id
        },
        setChapContents (state, contents) {
            if(state.chapContents == null){
                state.chapContents = contents;
            }else{
                state.chapContents = state.chapContents.concat(contents);
            }
        }
    }
})