
<template>
    <div id="listchapter">
        <h2 class="title clearfix">
            <i class="fa fa-list"></i> Danh sách chương
        </h2>
        <ul class="list-group list-group-flush">
            <li class="list-group-item">
                <div class="row">
                    <div class="col-5 no-wrap"><strong>Số chương</strong></div>
                    <div class="col-4 no-wrap text-center"><strong>Cập nhật</strong></div>
                    <div class="col-3 no-wrap text-center"><strong>PDF</strong></div>
                </div>
            </li>
            <li class="list-group-item" v-for="(item, index) in chapters" v-bind:key="index">
                <div class="row">
                    <div class="col-5 chapter">
                        <a href="javascript:void(0)" @click="chapterRead(index,item.id)" :data-id="item.id">{{item.name}}</a>
                    </div>
                    <div class="col-4 text-center no-wrap small">{{fromNow(item.updated.toDate())}}</div>
                    <div class="col-3 text-center small"><button class="btn btn-sm btn-link btn-pdf"><i class="fa fa-cloud-download fa-2" aria-hidden="true"></i></button></div>
                </div>
            </li>
        </ul>
    </div>
</template>
<script>
/* eslint-disable */
import {b64_to_utf8} from '../utils/safe-base64';
import { collection, doc, setDoc, getDoc, getDocs, query, orderBy } from "firebase/firestore"; 
import { mapState } from 'vuex'
import moment from 'moment';

import HelloWorld from './HelloWorld.vue';

export default {
    name: 'ListChapter',
    data() {
        return {
            chapters: {}
        }
    },
    computed: mapState({
        postId: state => state.postId,
        db: state => state.db,
    }),

    created() {
        this.getChapterData();
    },
    methods: {
        chapterRead: function(index,id){
            //console.log(index,id);
            //console.log(JSON.parse(b64_to_utf8(this.chapters[index].content)));
            let _this = this;
            $("body").addClass("is-modal");
            this.$FModal.show(
                { 
                    component: HelloWorld,
                    width: '100%',
                    clickToClose: false,
                    maxHeight: '100%', 
                    placement: 'top center',
                    modalStyles: { 
                        position: 'fixed',
                        top:0 
                    },
                    contentStyles: {
                        position: 'fixed',
                        top: 0,
                        borderRadius: 0,
                        height: '100vh'
                    },
                    animation: 'fade-up'
                },
                { 
                    chapters: _this.chapters.slice().reverse(),
                    selectId: id
                }
            );
        },
        fromNow: function(date){
            moment.locale('vi');
            return moment(date).fromNow();
        },
        getChapterData: async function(){
            let chaptersRef = collection(this.db, "chapters", this.postId, "infos");
            
            const q = query(chaptersRef, orderBy("index", "desc"));
            const querySnapshot = await getDocs(q);
            this.chapters = querySnapshot.docs.map((d) => ({id:d.id, ...d.data()}))
            //console.log(this.chapters);
            // let docRef = doc(chaptersRef, "chapters");
            
            // let docSnap = await getDoc(docRef);
            // let data = null;
            // if (docSnap.exists()) {
            //     data = docSnap.data();
            //     console.log("Document data:", docSnap.data());
            // } else {
            //     // doc.data() will be undefined in this case
            //     console.log("No such document!");
            // }
        }
    }
}
</script>
<style lang="scss">
#listchapter{
    .list-group{
        .list-group-item{
            color: var(--post-text-color);
            border-color: gray;
            .btn-pdf{
                .fa{
                    font-size: 20px;
                }
            }
        }
        [class="list-group-item"] {
            background-color: transparent;
        }
    }
}
</style>