<template>
  <div class="chapter row" ref="chapter">
    <div class="toolbar top">
      <button class="btn btn-link close-btn" @click="closeModal">Close <i class="fa fa-window-close"
          aria-hidden="true"></i></button>
      <div class="col-12 d-flex align-items-center justify-content-center">
        <button v-bind:disabled="currentIndex == 0" class="btn btn-secondary btn-sm btn-prev" @click="preChap"><i
            class="fa fa-chevron-left" aria-hidden="true"></i></button>
        <select @change="changeChap" v-model="currentIndex" style="width:140px" class="form-select form-select-sm mx-2"
          aria-label="Default select example">
          <option v-for="(chap, index) in  chapters" :key="index" v-bind:value="index">{{ chap.name }}</option>
        </select>
        <button v-bind:disabled="currentIndex >= chapters.length - 1" class="btn btn-danger btn-sm btn-next"
          @click="nextChap"><i class="fa fa-chevron-right" aria-hidden="true"></i></button>
      </div>

    </div>
    <div id="chapter-content" class="content" ref="content">
      <div v-for="(item, index) in chapImages" :key="index" class="page">
        <!-- <img v-if="index > 3" :src="GetImgSrc(item)" /> -->
        <img v-if="index > 0" v-lazy="GetImgSrc(item)" />
      </div>
    </div>
    <div class="toolbar bottom">
      <div class="col-12 d-flex align-items-center justify-content-center">
        <button v-bind:disabled="currentIndex == 0"
          class="btn btn-secondary btn-sm btn-prev d-flex align-items-center justify-content-center" @click="preChap"><i
            class="fa fa-chevron-left" aria-hidden="true"></i><span class="ms-2">Chap trước</span></button>
        <span class="mx-4"></span>
        <button v-bind:disabled="currentIndex >= chapters.length - 1"
          class="btn btn-danger btn-sm btn-next d-flex align-items-center justify-content-center" @click="nextChap"><i
            class="fa fa-chevron-right" aria-hidden="true"></i><span class="ms-2">Chap sau</span></button>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import { async, base64Decode } from '@firebase/util';
import { mapState } from 'vuex'
import { collection, doc, setDoc, getDoc, getDocs, query, orderBy, where  } from "firebase/firestore";
export default {
  name: 'HelloWorld',
  props: {
    msg: String,
    chapters: {},
    selectId: '',
  },
  computed: mapState({
    postId: state => state.postId,
    chapContents: state => state.chapContents,
    db: state => state.db,
  }),
  data: function () {
    return {
      comicInfo: {},
      currentChap: {},
      //chapContents: null,
      chapImages: {},
      currentIndex: 0,
    }
  },
  methods: {
    closeModal: function () {
      $("body").removeClass("is-modal");
      this.$FModal.hide();
    },
    substringBetween(s, a, b) {
      var p = s.indexOf(a) + a.length;
      return s.substring(p, s.indexOf(b, p));
    },
    randomNumber: function () {
      return Math.floor(Math.random() * (9 - 1 + 1)) + 1;
    },
    GetImgSrc(uri) {
      //return "#";
      const servers = [];
      servers[0] = "cdn.ntcdntempv26.com";
      servers[1] = "cdn.ntcdntempv3.com";
      servers[2] = 'i441.ntcdntempv26.com';
      servers[3] = "i221.ntcdntempv3.com";
      //let s = Math.floor(Math.random() * (3 - 1 + 1)) + 1;
      let s = this.substringBetween(uri, '//', '/data')
      if (s.includes('ntcdntempv3')) {
        uri = uri.replace(s, servers[1]);
      } else if (s.includes('ntcdntempv26')) {
        uri = uri.replace(s, servers[0]);
      }
      //console.log(s,uri);
      //https://images2-focus-opensocial.googleusercontent.com/gadgets/proxy?container=playdjcom&no_expand=1&refresh=86400&url=https://archive.org/download/26-07-2020-1/index-2-full-hd.png
      //https://images2-focus-opensocial.googleusercontent.com/gadgets/proxy?container=play3232net&no_expand=1&refresh=86400&url=https://archive.org/download/21-02-2020/file0.m4s
      //images-blogger-opensocial.googleusercontent.com
      //let urlBase = 'https://images' + this.randomNumber() + '-focus-opensocial.googleusercontent.com/gadgets/proxy?container=focus&gadget=a&no_expand=1&resize_h=0&rewriteMime=image%2F*&url=';
      let urlBase = 'https://images-blogger-opensocial.googleusercontent.com/gadgets/proxy?container=blogger&gadget=a&refresh=2592000&rewriteMime=image%2F*&url=';
      //let urlBase = 'https://images-onepick-opensocial.googleusercontent.com/gadgets/proxy?container=onepick&refresh=2592000&url='
      //let urlBase = 'https://www-sites-opensocial.googleusercontent.com/gadgets/proxy/refresh=2628000&container=enterprise/';
      //let img = 'https:' + uri;
      //let urlBase = 'https://cdn-vietpik-focus-opensocial.googleusercontent.com/gadgets/proxy?container=focus&url=';
      //let urlBase = 'http://images1-prose-opensocial.googleusercontent.com/gadgets/proxy?container=prose&gadget=a&rewriteMime=image/*&no_expand=1&url='
      //let urlBase = 'http://images1-pos-opensocial.googleusercontent.com/gadgets/proxy?container=pos&gadget=a&rewriteMime=image/*&no_expand=1&url='

      let img = encodeURIComponent('https:'+uri)
      if (img != 'undefined') {
        return urlBase + img;
      }
      //console.log(uri);
      return uri;
    },
    changeChap(event) {
      //console.log(event.target);
      this.currentIndex = event.target.value;
    },
    nextChap() {
      if (this.currentIndex < this.chapters.length - 1) {
        this.currentIndex++;

      }
    },
    preChap() {
      if (this.currentIndex > 0) {
        this.currentIndex--;

      }
    },
    setChapImages: async function () {
      //console.log("setChapImages");
      //this.chapImages = JSON.parse(base64Decode(this.currentChap.content));
      let docRef = doc(this.db, "chapters", this.postId, "chapters", this.currentChap.id, "contents", "images");
      const docSnap = await getDoc(docRef);

      if (docSnap.exists()) {
        //console.log("Document data:", docSnap.data());
        this.chapImages = JSON.parse(base64Decode(docSnap.data().content));
      } else {
        // doc.data() will be undefined in this case
        console.log("No such document!");
        this.chapImages = {}
      }
    },
    setChapImages2: async function () {
      if(this.chapContents == null)
        await this.getChapContents(0);
      let findChap = this.chapContents.find(c => c.id == this.currentChap.id);
      if(findChap == null){
        await this.getChapContents(this.currentChap.index)
      }
      //console.log(this.chapContents);
      findChap = this.chapContents.find(c => c.id == this.currentChap.id);
      this.chapImages = JSON.parse(base64Decode(findChap.images));
    },
    getChapContents: async function (index) {
      let chaptersRef = collection(this.db, "chapters", this.postId, "contents");

      const q = query(chaptersRef, where("index", "<=", index + 500));
      const querySnapshot = await getDocs(q);
      let arr = querySnapshot.docs.map((d) => ({ id: d.id, ...d.data() }));

      this.$store.commit('setChapContents', arr);
      // if(this.chapContents === null){
      //   this.chapContents = arr;
      // }else{
      //   this.chapContents = this.chapContents.concat(arr);
      // }
      //console.log(this.chapContents);
      
    }
  },
  created: async function() {
    this.currentChap = this.chapters.find(c => c.id == this.selectId);
    this.currentIndex = this.chapters.findIndex(c => c.id == this.selectId);
    if (this.currentIndex == 0) {
      this.setChapImages2();
      //this.getChapContents();
    }

  },
  mounted() {
    //this.$refs.content.scrollTop = 0;
    // this.currentChap = this.chapters.find(c => c.id == this.selectId);
    // this.currentIndex = this.chapters.findIndex(c => c.id == this.selectId);
    //this.setChapImages();
  },
  watch: {
    currentIndex(newId, oldId) {
      let _this = this;
      //this.$refs.content.style.display = "none";
      this.$refs.content.scrollTop = 0;
      _this.currentChap = _this.chapters[newId];
      this.setChapImages2();
      //window.scrollTo(0, 0);
    }
  },
}
</script>
